* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
}

.popup {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(18, 18, 18, 0.5);
}
.popup .alert {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 910px;
  height: 210px;
  background-color: #fff;
}
.popup .alert .close {
  position: absolute;
  top: 46px;
  right: 46px;
  height: 12px;
  width: 12px;
  cursor: pointer;
}
.popup .alert .close .vector {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
  height: 2px;
  width: 16px;
  background-color: #000;
  border-radius: 25px;
}
.popup .alert .close .second {
  transform: translate(-50%, -50%) rotate(45deg);
}
.popup .alert .alert-content {
  margin: 56px 0 0 95px;
}
.popup .alert .alert-content h2 {
  font-size: 42px;
  margin-bottom: 24px;
  line-height: 120%;
  font-weight: 700;
}
@media screen and (max-width: 500px) {
  .popup .alert .alert-content h2 {
    font-size: 36px;
  }
}
.popup .alert .alert-content p {
  font-weight: 300;
  line-height: 150%;
}
@media screen and (max-width: 1000px) {
  .popup .alert .alert-content p {
    text-align: justify;
  }
}
@media screen and (max-width: 500px) {
  .popup .alert .alert-content p {
    font-size: 14px;
  }
}
.popup .alert .alert-content p strong {
  font-weight: 700;
}
.popup .alert .alert-content .btn {
  display: none;
  width: 128px;
  height: 48px;
  border: none;
  border-radius: 32px;
  background-color: #E4C1AE;
  color: white;
  font-weight: 700;
  cursor: pointer;
  margin-left: 20px;
  width: 100px;
  height: 32px;
}
.popup .alert .alert-content .show-btn {
  display: inline-block;
}
@media screen and (max-width: 600px) {
  .popup .alert .alert-content .show-btn {
    display: block;
    margin: 15px auto 0 auto;
  }
}
@media screen and (max-width: 600px) {
  .popup .alert .alert-content {
    height: 100%;
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}
@media screen and (max-width: 1000px) {
  .popup .alert {
    width: 95%;
  }
}

.popup.show {
  display: block;
}

.container {
  width: 1440px;
  margin: 100px auto;
  font-family: "Roboto", sans-serif;
}
.container .view {
  display: flex;
  justify-content: space-around;
  width: 70%;
  margin: 0 auto;
}
@media screen and (max-width: 1440px) {
  .container .view {
    width: 80%;
  }
}
@media screen and (max-width: 1200px) {
  .container .view {
    width: 90%;
  }
}
@media screen and (max-width: 1000px) {
  .container .view {
    flex-direction: column;
  }
}
@media screen and (max-width: 1440px) {
  .container {
    width: 100%;
  }
}

.picture img {
  width: 505px;
}
@media screen and (max-width: 600px) {
  .picture img {
    width: 100%;
  }
}
@media screen and (max-width: 1200px) {
  .picture {
    margin-right: 10px;
  }
}
@media screen and (max-width: 1000px) {
  .picture {
    align-self: center;
    margin-bottom: 20px;
    margin-right: 0px;
  }
}

.content {
  width: 380px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.content h2 {
  font-size: 42px;
  margin-bottom: 24px;
  line-height: 120%;
  font-weight: 700;
}
@media screen and (max-width: 500px) {
  .content h2 {
    font-size: 36px;
  }
}
.content p {
  font-weight: 300;
  line-height: 150%;
  margin-bottom: 32px;
}
@media screen and (max-width: 1000px) {
  .content p {
    text-align: justify;
  }
}
@media screen and (max-width: 500px) {
  .content p {
    font-size: 14px;
  }
}
.content button {
  width: 128px;
  height: 48px;
  border: none;
  border-radius: 32px;
  background-color: #E4C1AE;
  color: white;
  font-weight: 700;
  cursor: pointer;
}
@media screen and (max-width: 1000px) {
  .content {
    align-self: center;
    justify-content: flex-start;
    align-items: center;
  }
}
@media screen and (max-width: 500px) {
  .content {
    width: 100%;
  }
}